import React, { PropTypes } from 'react';
import { TableRow, TableRowColumn } from 'material-ui/Table';
import StarIcon from 'react-icons/lib/md/star';
import css from './Song.scss';
import muiThemeable from 'material-ui/styles/muiThemeable';

const Song = ({ songValues, columnWidths, muiTheme, ...custom }) => {
  return (
    <TableRow
      hoverable={true}
      displayBorder={true}
      {...custom}
    >
      <TableRowColumn
        data-rowId={songValues.id}
        style={columnWidths[0]}
        className={css.title}>
        { songValues.title }
      </TableRowColumn>
      <TableRowColumn
        data-rowId={songValues.id}
        style={columnWidths[1]}
        className={css.artist}>
        { songValues.artist.fullName }
      </TableRowColumn>
      <TableRowColumn
        data-rowId={songValues.id}
        style={columnWidths[2]}
        className={css.progress}>
        { [...Array(songValues.progress)].map((x,i) =>
          <StarIcon key={i} style={{color: muiTheme.starColor}} />
        ) }
      </TableRowColumn>
      <TableRowColumn
        data-rowId={songValues.id}
        style={columnWidths[3]}
        className={css.difficulty}>
        { songValues.difficulty }
      </TableRowColumn>
  </TableRow>
  );
};

Song.propTypes = {
  songValues: PropTypes.shape({
    completed: PropTypes.bool,
    title:     PropTypes.string.isRequired,
    progress:  PropTypes.number,
    difficulty: PropTypes.number
  }),
  columnWidths: PropTypes.array
};

export default muiThemeable()(Song);
